import request from '@/utils/request'
import { getToken } from '@/utils/auth'
var qs = require('qs') // 

/**
 *banner
 * @param {*} postData 
 */
export function getBannerList (postData) {
  postData = postData || {}
  const token = getToken()
  console.log(token)
  postData.token = token
  return request({
    url: '/Api/Product/getBannerList',
    method: 'post',
    data: qs.stringify(postData)
  })
}

/**
 *
 * @param {*} postData 
 */
export function getActiveProductList (postData) {
  postData = postData || {}
  const token = getToken()
  console.log(token)
  postData.token = token
  return request({
    url: '/Api/ProductClient/getActiveProductList',
    method: 'post',
    data: qs.stringify(postData)
  })
}



