import request from '@/utils/request'
import { getToken } from '@/utils/auth'
var qs = require('qs') // 

/**
 * token
 * @param {*} postData 
 */
export function getWeixinToken (postData) {
  postData = postData || {}
  return request({
    url: '/Api/System/getWeixinToken',
    method: 'post',
    data: qs.stringify(postData)
  })
}
